import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
// import About from '@/views/About'
import Hello from '@/components/Hello'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          path: 'user',
          name: 'home',
          component: Hello
        }
      ]
    }
  ]
})



